<template>
  <div class="home">
    <!-- MODAL -->
    <div v-if="modal" @click="modal=false" style="position:fixed;width:100vw;height:100vh;left:0;top:0;z-index:9998;"></div>
    <div v-if="modal" style="position: fixed;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:9999;min-width:80vw;">
        <div class="rounded-lg shadow p-3 bg-white animate__animated animate__bounceIn">
            <p class="text-sm font-semibold">List Data ({{item.table}})</p>
            <hr>
            <div v-for="(item, index) in boxes"  :key="index+'boxes'">
                <p class="m-0 p-0 text-xs">Field : 
                  <!-- <input type="text" class="d-inline text-xs" style="border:none;" v-model="boxes[index]['field']"> -->
                  {{item.field}}
                  </p>
                <table>
                  <tr>
                    <td class="p-0 m-0 text-xs">Text</td>
                    <td class="px-2">:</td>
                    <td>
                      <input type="text" class="d-inline text-xs" style="border:none;width: 100%;" v-model="boxes[index]['text']">
                    </td>
                  </tr>
                </table>
                <hr>
            </div>
            <hr>
            <button type="button" @click="saveData" class="btn btn-sm btn-success">Simpan</button>
        </div>
    </div>
        <!-- MODAL -->
    <div style="position: fixed; top: 0; left: 0; z-index: 9999">
      <div class="spinner-container">
        <div class="spinner"></div>
      </div>
    </div>
    <div style="position:absolute;z-index:999;top:0;left:0;">
     <div
        v-for="(rect, index) in scaledBoxes"
        :key="index"
        :style="getRectangleStyles(rect)"
        class="rectangle"
      ></div>
    </div>
    <video id="video" autoplay muted playsinline></video>
    <div class="row justify-content-center" style="position:fixed;bottom:20px;left:0;width:100%;">
         <div class="col-4 text-center">
            <button id="captureBtn" @click="capture" class="btn btn-lg rounded-circle btn-success text-lg pt-3 pb-2" style="font-size:20px;"><i class="text-lg fa fa-camera" style="font-size:20px;" aria-hidden="true"></i></button>
        </div>
    </div>
    <br>
    <canvas id="canvasElement" style="display:none;"></canvas>
    <img id="photoElement" src="" style="display:none;position:absolute;z-index:-1;left:-10000;" alt="Captured Photo">
    <div id="cropContainer">
    </div>
  </div>
</template>
<script>
import auto from "@/plugins/autophp"
let sdb=new auto()
export default {
    data() {
        return {
            boxes:[],
            scaledBoxes:[],
            height:1920,
            width:1080,
            item:{},
            scan:false,
            modal:false,
            listCropImage: [],
            listHasil:[]
        }
    },
    layout:'camera',
  methods: {
    async saveData(){
        let that=this;
        const dataCollection = firebase.collection(firebase.firestore, that.item.table);
        let obj={}
        that.boxes.forEach(e=>{
            obj[e.field]=e.text
        })
        await firebase.addDoc(dataCollection, obj)
        .then((docRef) => {
            console.log('Layouts added with ID: ', docRef.id);
        })
        .catch((error) => {
            console.error('Error adding Layouts: ', error);
        });
        that.modal=false;
        that.$router.push(`/database?table=${that.item.table}&id=${that.$route.query.id}`)
    },
     capture() {
            let that=this;
            sdb.loadingOn()
            const canvas = document.getElementById('canvasElement');
            const photoElement = document.getElementById('photoElement');
            canvas.width = video.videoWidth;
            canvas.height = video.videoHeight;
            canvas.getContext('2d').drawImage(video, 0, 0, canvas.width, canvas.height);
            const photoURL = canvas.toDataURL('image/jpeg');
            photoElement.src = photoURL;
            setTimeout(() => {
                that.cropPhoto()
            }, 1000);
            // this.sendToApi(photoURL)
        },
        cropPhoto() {
            let that=this;
            console.log('start croping')
            const imgElement = document.getElementById('photoElement');
            const canvas = document.createElement('canvas');
            const ctx = canvas.getContext('2d');
            const img = new Image();
            let container=document.getElementById('cropContainer')
            img.onload = () => {
                let k=this.boxes.length-1
                for (let i=0;i<this.boxes.length;i++){
                // for (const box of this.boxes) {
                    var { x, y, width, height } = this.boxes[i];
                    canvas.width = width;
                    canvas.height = height;
                    x=that.item.dimension=='landscape'?x-60:x
                    y=that.item.dimension=='landscape'?y-30:y
                    ctx.drawImage(img, x, y, width, height, 0, 0, width, height);
                    const dataUrl = canvas.toDataURL();
                    const croppedImage = new Image();
                    croppedImage.src = dataUrl;
                    // container.appendChild(croppedImage)
                    that.sendToApi(dataUrl,i)
                    if(k==i){
                        setTimeout(() => {
                            sdb.loadingOff()
                            that.modal=true
                        }, 3000);
                    }
                }
            };
            img.setAttribute('crossorigin', 'anonymous'); // works for me
            img.src = imgElement.src;
        },
        async sendToApi(photoURL,i){
            let that=this;
            const apiUrl = 'https://hit-ocr.online/api/upload-ocr'; // Replace this with the actual API endpoint
            function dataURLtoFile(dataURL, filename) {
                const arr = dataURL.split(',');
                const mime = arr[0].match(/:(.*?);/)[1];
                const bstr = atob(arr[1]);
                let n = bstr.length;
                const u8arr = new Uint8Array(n);
                while (n--) {
                    u8arr[n] = bstr.charCodeAt(n);
                }
                return new File([u8arr], filename, { type: mime });
            }
            const file = dataURLtoFile(photoURL, `image${i}.jpg`);
            const formData = new FormData();
            formData.append('file', file);
            let hasil=await axios.post(apiUrl,formData)
            that.item.boxes[i]['text']=hasil.data.text.join(' ')
        },
        getRectangleStyles({ x, y, width, height }) {
            return {
            position: 'absolute',
            left: `${x}px`,
            top: `${y}px`,
            width: `${width}px`,
            height: `${height}px`,
            border: '1px solid red',
            };
      },
    startVideo() {
      let that = this;
      const video = document.getElementById("video");
      navigator.mediaDevices
        .getUserMedia({
          video: {
            facingMode: "environment",
            width: { ideal: that.item.dimension=='landscape'?that.width:that.height },
            height: { ideal: that.item.dimension=='landscape'?that.height:that.width },
          },
        })
        .then(function (mediaStream) {
          that.$forceUpdate();
          video.srcObject = mediaStream;
          video.play();
           function scaleBoxes(boxes, sourceWidth, sourceHeight, targetWidth, targetHeight) {
                const scaleX = targetWidth / sourceWidth;
                const scaleY = targetHeight / sourceHeight;

                const scaledBoxes = boxes.map((box) => ({
                    x: Math.round(box.x * scaleX),
                    y: Math.round(box.y * scaleY),
                    width: Math.round(box.width * scaleX),
                    height: Math.round(box.height * scaleY),
                }));

                return scaledBoxes;
            }
            const boxes = that.boxes
            setTimeout(() => {
                const video2 = document.getElementById("video");
                const sourceWidth = that.item.dimension=='landscape'?that.height:that.width;
                const sourceHeight = that.item.dimension=='landscape'?that.width:that.height;
                const targetWidth = video2.clientWidth;
                const targetHeight = video2.clientHeight;
                const scaledBoxes = scaleBoxes(boxes, sourceWidth, sourceHeight, targetWidth, targetHeight);
                that.scaledBoxes=scaledBoxes
                sdb.alert(`Posisikan Camera ${that.item.dimension}`,'bg-yellow-800')
            }, 2000);
        });
    },
     async getData(){
          let that=this;
          const docRef = firebase.doc(firebase.firestore, 'ocr-layouts', that.$route.query.id);
          firebase.getDoc(docRef)
            .then((docSnapshot) => {
              if (docSnapshot.exists()) {
                let obj=docSnapshot.data();
                that.item=obj;
                that.height=obj.height
                that.width=obj.width
                if(obj.boxes){
                  that.boxes=obj.boxes
                }
                that.imageUrl=`https://hit-ocr.online/uploaded/`+obj.filename
                console.log('Document data:', docSnapshot.data());
                that.startVideo();
              } else {
                console.log('Document does not exist');
              }
            })
            .catch((error) => {
              console.error('Error getting document:', error);
            });
      },
  },
  mounted() {
    this.getData()
  },
};
</script>
<style>
.spinner-container {
  background-color: transparent; /* Transparent background */
}

.spinner {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 5px solid white;
  border-top-color: black;
  animation: spin 1s infinite linear;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.crop-image-container {
  display: inline-block;
  margin: 10px;
  border: 1px solid black;
}
.crop-image-container img {
  width: 100%;
  height: auto;
}
body {
  margin: 0;
  overflow: hidden;
}
video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;
  display: inline-block;
}
.rectangle {
  background-color: rgba(255, 0, 0, 0.5);
}
</style>
